import React, { useState, useEffect, useRef } from "react";
import { Col, Row, Container } from "react-bootstrap";
import { navigate } from "gatsby";

import "../components/pages-css/sCita.css";
import Layout from "../components/layout.js";
import Seo from "../components/seo";
import VideoSeccion from "../components/videoSeccion.js";
import api from "../components/actions/riveroapi";

import diagnostico from "../images/iconos/btn_diagnostico.svg";

export default function ImprimirDiagnostico(){

    const refForm = useRef();
    const [mensajeModal, setMensajeModal] = useState("");
    const windowGlobal = typeof window !== 'undefined' && window;

    useEffect(() => {
        let diagnostico = true;
        api.postCrearCita(diagnostico).then((respCrear) => {
            //console.log("Response del sinCita", respCrear);

            api.enviaCorreoOrden(respCrear.result.data.htracabe[0].id, diagnostico).then((resp) => {
                //console.log(resp);
                navigate("/");
                var myItem = windowGlobal.localStorage.getItem('token');
                windowGlobal.localStorage.clear();
                windowGlobal.localStorage.setItem('token', myItem);
            });


        }).catch((error) => {
            //console.log("Hubo un error al registar la cita", error); 20295352
            setMensajeModal(<center>
                                <div>❌ Error, no se pudo crear tu orden de servicio, contacta a un agente. </div>
                            </center>);
        })

    }, []);

    return(
        <Layout>
            <Seo title="Auto servicio" />
            <Container className="container-sin-cita">
                <Row className="row-cita-arriba">
                    <div className="container-cita">
                        <Container className="p-5">
                            <Row>
                                <Col lg={3} className="col-diagnostico">
                                    <Row className="btn-celeste-grande" style={{width: '100% !important'}}>
                                        <div style={{height: '80%', display: 'flex', justifyContent: 'center'}}>
                                            <img src={diagnostico} className="img-servicios" style={{ marginBottom: 0}} />
                                        </div>
                                        <div className="flexCenter"><h6 style={{ marginTop: 0}}>Imprimir diagnóstico</h6></div>
                                    </Row>
                                </Col>
                                <Col lg={9} className="flexCenter">
                                    <Row className="btn-naranja-diagnostico" >
                                        <div className="flexCenter">
                                            <h5 className="titulo-h1-blanco">Chevolet Rivero Agradece su preferencia.</h5>
                                        </div>
                                        <div style={{display: 'flex', justifyContent: 'center', flexDirection: 'column', paddingLeft: 60}}>
                                            <p>. Tome su ticket </p>
                                            <p>. Solicite orden de reparación impresa </p>
                                        </div>
                                    </Row>
                                </Col>
                            </Row>
                        </Container>
                    </div>
                </Row>
                <Row className="row-cita-abajo">
                    <VideoSeccion refForm={refForm} none="none"/>
                </Row>
            </Container>
        </Layout>
    )

}